import axios from "axios";

const state = {
  plants: [],
  plant: null,
};
const getters = {
  plants(state) {
    return state.plants;
  },
};
const mutations = {
  plantsRequest(state) {
    state.status = "loading";
  },
  plantsSuccess(state, payload) {
    state.status = "success";
    state.plants = payload.data.plants;
  },
  plantsError(state, err) {
    state.status = err.response.data.message;
  },
  plantRequest(state) {
    state.status = "loading";
  },
  plantSuccess(state, payload) {
    state.status = "success";
    state.plant = payload.data.plant;
  },
  plantError(state, err) {
    state.status = err.response.data.message;
  },
  plantImageRequest(state) {
    state.status = "loading";
  },
  plantImageSuccess(state) {
    state.status = "success";
    // SAVE IMAGE TO PUBLIC IMAGE FOLDER
  },
  plantImageError(state, err) {
    state.status = err.response.data.message;
  },
};
const actions = {
  async getPlants({ commit }, payload) {
    try {
      commit("plantsRequest");
      const query = {
        // name: payload.name,
        genus: payload.genus,
        // potSize: payload.potSize,
        // price: payload.price,
        // description: payload.description,
        // available: payload.available,
      };
      const plants = await axios.get("/api/v1/plants", { params: query });
      commit("plantsSuccess", plants);
    } catch (err) {
      commit("plantsError", err);
    }
  },
  async getPlant({ commit }, payload) {
    try {
      commit("plantRequest");
      const plant = await axios.get(`/api/v1/plants/${payload}`);
      commit("plantSuccess", plant);
    } catch (err) {
      commit("plantError", err);
    }
  },
  async getPlantImage({ commit }, payload) {
    try {
      commit("plantImageRequest");
      const res = await axios.get(
        `/api/v1/plants/images/${payload.image_name}`,
        { responseType: "arraybuffer" }
      );
      const plantImage = Buffer.from(res.data, "binary").toString("base64");
      commit("plantImageSuccess", plantImage);
    } catch (err) {
      commit("plantImageError", err);
    }
  },
};

export default {
  state,
  getters,
  mutations,
  actions,
};
