<template>
  <div id="navbar">
    <div class="nav-item">
      <router-link :to="{ name: 'Home' }">
        <div class="nav-items">
          <img
            src="../assets/company-logo.svg"
            alt="logo"
            class="company-logo nav-item"
          />
          <span class="company-title nav-item">Plant Store</span>
        </div>
      </router-link>
    </div>
    <div class="nav-items">
      <div class="nav-item" v-if="!isLoggedIn">
        <router-link :to="{ name: 'Register' }">Register</router-link>
      </div>
      <div class="nav-item" v-if="!isLoggedIn">
        <router-link :to="{ name: 'Login' }">Login</router-link>
      </div>
      <div class="nav-item" v-if="isLoggedIn" v-on:click="logoutUser">
        <span>Log Out</span>
      </div>
      <div class="nav-item cart" v-if="numItemsInCart">
        <router-link :to="{ name: 'Cart' }"
          ><span class="material-icons"> shopping_cart </span
          >{{ numItemsInCart }}</router-link
        >
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from "vuex";
export default {
  name: "navbar",
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["isLoggedIn", "numItemsInCart"])
  },
  methods: {
    ...mapMutations(["updateCartQuantity"]),
    ...mapActions(["logout"]),
    async logoutUser() {
      await this.logout();
      this.updateCartQuantity();
    }
  },
  created() {
    this.updateCartQuantity();
  }
};
</script>

