const state = {
  status: "",
  numItemsInCart: 0,
  cart: {},
};
const getters = {
  numItemsInCart(state) {
    return state.numItemsInCart;
  },
};
const mutations = {
  addToCart(state, payload) {
    if (localStorage.cart) {
      let cartObj = JSON.parse(localStorage.cart);
      state.cart = cartObj;
    } else {
      state.cart = {};
    }

    if (state.cart[payload.plant._id]) {
      state.cart[payload.plant._id] += payload.quantity;
    } else {
      state.cart[payload.plant._id] = payload.quantity;
    }

    localStorage.setItem("cart", JSON.stringify(state.cart));
  },
  updateCart(state, { plant, quantity }) {
    const cartObj = JSON.parse(localStorage.cart);
    cartObj[plant] = quantity;
    localStorage.setItem("cart", cartObj);
    state.cart = cartObj;
  },
  updateCartQuantity(state) {
    if (localStorage.cart) {
      const plants = JSON.parse(localStorage.cart);
      const quantities = Object.values(plants);

      let items = 0;
      for (const index in quantities) {
        items += quantities[index];
      }
      state.numItemsInCart = items;
    } else {
      state.numItemsInCart = 0;
    }
  },
};
const actions = {};

export default {
  state,
  getters,
  mutations,
  actions,
};
